.example{
    width: 100%;
    height: 100%;
    display: flex;
    flex-wrap: wrap;
    align-content: center;
    margin: 0 auto;
    justify-content: space-evenly;
    background-color: #BDBDBD;
}
.example-container{
    width: 100%;
    height: 100%;
}
.example-container-title{
    margin: 0 auto;
    display: flex;
    justify-content: center;
    margin: 0 0 -70px 0;
}
.example-container-title-title{}
.example-container-container{
    width: 80%;
    height: 100%;
    display: flex;
    flex-wrap: wrap;
    align-content: center;
    margin: 0 auto;
    justify-content: space-evenly;
}
.example-container-content{
    width: 20%;
    height: 30%;
    margin: 3.5%;
    border: 1px solid #4f6d9c;
}
.example-container-content:hover{
}

@media (max-width: 821px) {
    .example-container-content{
        width: 360px;
        height: 360px;

    }
    .example-container-container {
        width: 100%;
        position: absolute;
        margin: 5px 0;
    }
}
